  from {
    opacity: 0;
    transform: translateY(50px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes section-title-on-leave {
  from {
    opacity: 1;
    transform: translateY(0px); }
  to {
    opacity: 0;
    transform: translateY(50px); } }

@keyframes statue-about-on-enter-mobile {
