/**/
/* FOOTER SECTION */
@keyframes footer-section-on-enter {
  from {
    opacity: 0;
    transform: translateY(100%); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes footer-section-on-leave {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0;
    transform: translateY(100%); } }

/**/
/* ABOUT FIRST SECTION */
@keyframes dots-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-40%); }
  to {
    opacity: 0.2;
    transform: translateX(-50%); } }

@keyframes dots-about-on-mobile-on-first-load {
  from {
    opacity: 0;
    transform: translateX(50px); }
  to {
    opacity: 0.2;
    transform: translateX(0); } }

@keyframes dots-about-on-leave {
  from {
    opacity: 0.2;
    transform: translateX(-50%); }
  to {
    opacity: 0;
    transform: translateX(-40%); } }

@keyframes indian-girl-on-first-load {
  from {
    opacity: 0;
    transform: translate(30px, 0); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes indian-girl-on-leave {
  from {
    opacity: 1;
    transform: translate(0, 0); }
  to {
    opacity: 0;
    transform: translate(30px, 0); } }

@keyframes title-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-30px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes title-about-on-leave {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(-30px); } }

@keyframes sub-title-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-75px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes sub-title-about-on-leave {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(-75px); } }

/**/
/* HAND ANIMATION */
@keyframes hand-move {
  0% {
    transform: translateX(20px); }
  50% {
    transform: translateX(-20px); }
  100% {
    transform: translateX(20px); } }

/**/
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(30px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-30px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

