@keyframes text-about-on-leave {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

/**/
/* THIRD SECTION */
@keyframes tabs-on-enter {
  from {
    opacity: 0;
    transform: translate(-100%, -50%); }
  to {
    opacity: 1;
    transform: translate(0, -50%); } }

@keyframes tabs-on-leave {
  from {
    opacity: 1;
    transform: translate(0, -50%); }
  to {
    opacity: 1;
    transform: translate(-100%, -50%); } }

@keyframes card-on-enter {
  from {
    width: 100%; }
  to {
    width: 0; } }

@keyframes card-on-leave {
  from {
    width: 0; }
  to {
    width: 100%; } }

@keyframes tab-is-enter-change {
  from {
    max-width: 0; }
  to {
    max-width: 100%; } }

@keyframes tab-is-leave-change {
  from {
    max-width: 100%; }
  to {
    max-width: 0; } }

@keyframes see-all-link-on-enter {
  from {
    opacity: 0;
    transform: translateY(15px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes see-all-link-on-leave {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0;
    transform: translateY(15px); } }

@keyframes see-all-line-on-enter {
