/* COLORS */
/* FIRST SECTION */
@keyframes statue-on-first-load {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes statue-on-leave {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(-50px); } }

@keyframes statue-on-enter {
  from {
    opacity: 0;
    transform: translateX(-50px); }
  to {
    opacity: 1;
    transform: translateX(0px); } }

@keyframes dots-on-first-load {
  from {
    opacity: 0;
    transform: translate(-50px, 0); }
  to {
    opacity: 1;
    transform: translate(0px, 0); } }

@keyframes dots-on-leave {
  from {
    transform: translate(0, 0); }
  to {
    transform: translate(0, 90px); } }

@keyframes dots-on-enter {
  from {
    transform: translate(0, 90px); }
  to {
    transform: translate(0, 0); } }

@keyframes titles-on-first-load {
  from {
    opacity: 0;
    transform: translate(-50px, 0); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes titles-on-leave {
  from {
    opacity: 1;
    transform: translate(0, 0); }
  to {
    opacity: 0;
    transform: translate(0, 50px); } }

@keyframes titles-on-enter {
  from {
    opacity: 0;
    transform: translate(0, 50px); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes see-more-text-on-first-load {
  from {
    transform: translateY(-250%); }
  to {
    transform: translateY(0); } }

@keyframes see-more-line-on-first-load {
  from {
    width: 0;
    background-color: #fff; }
  to {
    width: 100%;
    background-color: #d9255e; } }

@keyframes see-more-on-leave {
  from {
    bottom: 0;
    width: 312px; }
  to {
    bottom: 40%;
    width: 135px; } }

@keyframes see-more-on-enter {
  from {
    bottom: 40%;
    width: 135px; }
  to {
    bottom: 0;
    width: 312px; } }

@keyframes social-on-first-load {
  from {
    opacity: 0; }
  to {
    opacity: 0.3; } }

/**/
/* SECOND SECTION */
@keyframes section-title-on-enter {
  from {
    opacity: 0;
    transform: translateY(50px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes section-title-on-leave {
  from {
    opacity: 1;
    transform: translateY(0px); }
  to {
    opacity: 0;
    transform: translateY(50px); } }

@keyframes statue-about-on-enter-mobile {
  from {
    opacity: 0;
    transform: translateX(50px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes statue-about-on-leave-mobile {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(50px); } }

@keyframes statue-about-on-enter {
  from {
    opacity: 0;
    transform: translateX(200px); }
  to {
    opacity: 1;
    transform: translateX(110px); } }

@keyframes statue-about-on-leave {
  from {
    opacity: 1;
    transform: translateX(110px); }
  to {
    opacity: 0;
    transform: translateX(200px); } }

@keyframes text-about-on-enter {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(100%); } }

@keyframes text-about-on-leave {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

/**/
/* THIRD SECTION */
@keyframes tabs-on-enter {
  from {
    opacity: 0;
    transform: translate(-100%, -50%); }
  to {
    opacity: 1;
    transform: translate(0, -50%); } }

@keyframes tabs-on-leave {
  from {
    opacity: 1;
    transform: translate(0, -50%); }
  to {
    opacity: 1;
    transform: translate(-100%, -50%); } }

@keyframes card-on-enter {
  from {
    width: 100%; }
  to {
    width: 0; } }

@keyframes card-on-leave {
  from {
    width: 0; }
  to {
    width: 100%; } }

@keyframes tab-is-enter-change {
  from {
    max-width: 0; }
  to {
    max-width: 100%; } }

@keyframes tab-is-leave-change {
  from {
    max-width: 100%; }
  to {
    max-width: 0; } }

@keyframes see-all-link-on-enter {
  from {
    opacity: 0;
    transform: translateY(15px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes see-all-link-on-leave {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0;
    transform: translateY(15px); } }

@keyframes see-all-line-on-enter {
  from {
    background-color: #fff;
    width: 0; }
  to {
    background-color: #d9255e;
    width: 100%; } }

@keyframes see-all-line-on-leave {
  from {
    background-color: #d9255e;
    width: 100%; }
  to {
    background-color: #fff;
    width: 0; } }

/**/
/* FOOTER SECTION */
@keyframes footer-section-on-enter {
  from {
    opacity: 0;
    transform: translateY(100%); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes footer-section-on-leave {
  from {
    opacity: 1;
    transform: translateY(0); }
  to {
    opacity: 0;
    transform: translateY(100%); } }

/**/
/* ABOUT FIRST SECTION */
@keyframes dots-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-40%); }
  to {
    opacity: 0.2;
    transform: translateX(-50%); } }

@keyframes dots-about-on-mobile-on-first-load {
  from {
    opacity: 0;
    transform: translateX(50px); }
  to {
    opacity: 0.2;
    transform: translateX(0); } }

@keyframes dots-about-on-leave {
  from {
    opacity: 0.2;
    transform: translateX(-50%); }
  to {
    opacity: 0;
    transform: translateX(-40%); } }

@keyframes indian-girl-on-first-load {
  from {
    opacity: 0;
    transform: translate(30px, 0); }
  to {
    opacity: 1;
    transform: translate(0, 0); } }

@keyframes indian-girl-on-leave {
  from {
    opacity: 1;
    transform: translate(0, 0); }
  to {
    opacity: 0;
    transform: translate(30px, 0); } }

@keyframes title-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-30px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes title-about-on-leave {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(-30px); } }

@keyframes sub-title-about-on-first-load {
  from {
    opacity: 0;
    transform: translateX(-75px); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes sub-title-about-on-leave {
  from {
    opacity: 1;
    transform: translateX(0); }
  to {
    opacity: 0;
    transform: translateX(-75px); } }

/**/
/* HAND ANIMATION */
@keyframes hand-move {
  0% {
    transform: translateX(20px); }
  50% {
    transform: translateX(-20px); }
  100% {
    transform: translateX(20px); } }

/**/
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(30px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-30px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

#main-section {
  height: 100vh;
  position: relative; }
  #main-section .fp-scroller {
    height: 100%; }
  #main-section .container-fluid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    height: 100%; }
  #main-section .section-dots-wrap {
    top: 43%;
    left: 10px;
    width: 280px;
    height: 280px;
    opacity: 0;
    transform: translate(-50px, 0); }
    #main-section .section-dots-wrap.on-first-load {
      opacity: 0.5;
      transform: translate(0, 0); }
  #main-section .col-info {
    position: absolute;
    top: 45%;
    left: 0;
    transform: translateY(-50%);
    z-index: 6; }
    #main-section .col-info.on-first-load {
      animation: titles-on-first-load 3s cubic-bezier(0.64, 0.04, 0.35, 1) 0.3s both; }
  #main-section .statue-wrap {
    position: relative;
    width: 100%;
    max-width: 290px;
    margin: 0 auto;
    z-index: 1; }
    #main-section .statue-wrap.on-first-load {
      animation: statue-on-first-load 3s cubic-bezier(0.64, 0.04, 0.35, 1) 0.3s both; }
    #main-section .statue-wrap img {
      width: 100%;
      height: 100%; }
  #main-section .statue {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1; }
  #main-section .blob-shape {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: transform 1.5s cubic-bezier(0.64, 0.04, 0.35, 1); }
  #main-section .title, #main-section .sub-title {
    text-align: center; }
  #main-section .title {
    font-size: 40px;
    margin-bottom: 0; }
  #main-section .sub-title {
    font-size: 20px;
    letter-spacing: -0.22px;
    text-transform: none;
    margin-bottom: 5px;
    font-family: "Museo Sans Cyrl 100"; }
    #main-section .sub-title span {
      color: #d9255e; }

@media (min-width: 768px) {
  #main-section .statue-wrap {
    margin-right: 50px;
    max-width: 440px; }
  #main-section .section-dots-wrap {
    width: 545px;
    height: 545px;
    top: auto;
    bottom: -130px;
    left: 120px; }
  #main-section .title, #main-section .sub-title {
    margin-left: 105px;
    text-align: unset; }
  #main-section .title {
    font-size: 60px; }
  #main-section .sub-title {
    font-size: 30px; } }

@media (min-width: 992px) {
  #main-section .col-info {
    position: relative;
    top: 0;
    transform: none;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  #main-section .section-dots-wrap {
    top: 50%;
    transform: translate(-50px, -50%);
    bottom: auto;
    width: 500px;
    height: 500px; }
    #main-section .section-dots-wrap.on-first-load {
      transform: translate(0, -50%); }
  #main-section .statue-wrap {
    max-width: 395px;
    margin-right: auto; } }

@media (min-width: 1200px) {
  #main-section .statue-wrap {
    isolation: isolate;
    max-width: 500px;
    height: 85vh; }
    #main-section .statue-wrap.on-enter {
      animation: statue-on-enter 1s cubic-bezier(0.64, 0.04, 0.35, 1) 0s both; }
    #main-section .statue-wrap.on-leave {
      animation: statue-on-leave 1s cubic-bezier(0.64, 0.04, 0.35, 1) 0s both; }
    #main-section .statue-wrap .statue {
      object-fit: contain; }
  #main-section .col-info.on-enter {
    animation: titles-on-enter 1s cubic-bezier(0.64, 0.04, 0.35, 1) 0s both; }
  #main-section .col-info.on-leave {
    animation: titles-on-leave 1s cubic-bezier(0.64, 0.04, 0.35, 1) 0s both; }
  #main-section .section-dots-wrap {
    width: 520px;
    height: 520px; }
  #main-section .title, #main-section .sub-title {
    transform: translateY(-60px); }
  #main-section .title {
    font-size: 90px; }
  #main-section .sub-title {
    font-size: 40px;
    margin-bottom: 0; } }
