    padding-top: 80px; }
  #about-main-section .fp-scroller {
    height: 100%; }
  #about-main-section .section-title {
    color: #fff;
    font-family: "Museo Sans Cyrl 700";
    font-size: 30px;
    letter-spacing: 0.85px;
    text-transform: uppercase;
    margin-bottom: 15px; }
    #about-main-section .section-title.on-first-load {
      animation: title-about-on-first-load 2s cubic-bezier(0.64, 0.04, 0.35, 1) 0.2s both; }
    #about-main-section .section-title > span {
